    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    &::after {
      background: rgba(255, 255, 255, 0.6); } }
  &.animate .container {
    -webkit-transform: translateX(-50%) rotateY(45deg) translateZ(-50px);
    transform: translateX(-50%) rotateY(45deg) translateZ(-50px); } }

