/* Media Queries */
@media screen and (max-width: 77em) {
  .outer-nav {
    &.top {
      top: 15%; }
    &.bottom {
      bottom: 15%; } } }

@media screen and (max-width: 36.625em), screen and (max-height: 41.75em) {
  .outer-nav {
    &.top {
      top: 5%; }
    &.bottom {
      bottom: 5%; }
    &.horizontal {
      font-size: 1.7em;
      width: 6.2em;
      a {
        display: block;
        text-align: left; } } }
  /* Special Case */
  .effect-rotatetop .outer-nav.horizontal {
    width: 95%;
    bottom: auto;
    top: 50%;
    a {
      display: inline-block; } } }

@media screen and (max-width: 31em), screen and (max-height: 36.2em) {
  .outer-nav {
    &.horizontal, &.vertical {
      font-size: 1.2em;
      width: 6.8em; }
    &.right {
      right: auto;
      left: 50%; } } }

@media screen and (max-height: 31.6em) {
  .outer-nav a {
    margin-bottom: 20px; } }

.container-viewport {
  position: relative;
  width: 100%;
  height: 100vh;
